<template>
  <div>
    <markdown-page :text='str'></markdown-page>
    <div id="disqus_thread"></div>
  </div>
</template>
<script>
import blog from '../blog'
export default {
  name: 'article',
  data () {
    return {
      str: ''
    }
  },
  mounted () {
    console.log(this)
    this.str = blog[this.$route.params.name]
    if (window.DISQUS) {
      console.log(this.$route.params.name)
      window.reset(this.$route.params.name, 'http://liujians.me/' + this.$route.params.name, this.$route.params.name)
    }
  }
}
</script>



